@import "utils/variables";
@import "utils/swiper-bundle.min";

*,
*::before,
*::after {
    border: 0;
    box-sizing: border-box;
}

html,
body,
textarea {
    height: 100%;
    font-family: "SuisseIntl";

}

html {
    scrollbar-width: none;
    -ms-overflow-style: none;
}

html::-webkit-scrollbar {
    display: none;
    width: 0;
}

body {
    margin: 0;
    color: #131313;
    &.hidden {
        overflow: hidden;
    }
}

button {
    font-family: SuisseIntl;
}

a {
    text-decoration: none;
    color: inherit;
}

#overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    background-color: #131313;
    opacity: 0;
    z-index: 100;
    pointer-events: none;
    transition: all 0.3s ease;
    visibility: hidden;

    &.active {
        visibility: visible;
        opacity: 0.5;
        // pointer-events: auto;
    }
}

section {
    margin-top: 85px;
    margin-bottom: 85px;

    @media(max-width: 700px) {
        margin-top: 55px;
        margin-bottom: 55px;
    }

    &:last-child {
        margin-bottom: 100px;

        @media(max-width: 700px) {
            margin-bottom: 65px;
        }
    }
}

section.offers {
    margin-bottom: 0;
}


figure {
    margin: 0;
}

button {
    padding: 0;
    background-color: transparent;
}

// ul,
// ol {

//     li {
//         list-style: none;
//     }
// }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
    -webkit-appearance: none;
}

input {
    font-family: SuisseIntl;
}

.wrapper {
    min-height: 100%;
    // overflow: hidden;
    display: flex;
    flex-direction: column;
}

main {
    flex: 1 1 auto;
}


._container {
    max-width: 1324px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding-left: 42px;
    padding-right: 42px;

    @media(max-width: 1240px) {
        padding-left: 42px;
        padding-right: 42px;
    }

    @media(max-width: 940px) {
        padding-left: 34px;
        padding-right: 34px;
    }

    @media(max-width: 700px) {
        padding-left: 30px;
        padding-right: 30px;
    }

    &--abs {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        height: 100%;
    }

    &--mode {
        @media (max-width: 700px) {
            padding: 0;
        }
    }
}

.__container-s {
    max-width: 820px;
    width: 100%;
    margin-left: auto;

    @media (max-width: 1240px) {
        max-width: 620px;
    }

    @media (max-width: 940px) {
        max-width: 100%;
        margin-left: 0;
    }
}

.__container-2cols {
    display: grid;
    grid-template-columns: 925px auto;
    grid-gap: 20px;
    align-items: flex-start;

    @media (max-width: 1240px) {
        grid-template-columns: 700px auto;
    }

    @media (max-width: 940px) {
        display: block;
    }
}

.__inner {
    display: grid;
    grid-template-columns: auto 400px;
    grid-gap: 20px;
    align-items: flex-start;

    @media(max-width: 1240px) {
        grid-template-columns: auto 300px;
    }

    @media(max-width: 940px) {
        grid-template-columns: 100%;
        grid-gap: 60px;
    }

    @media(max-width: 700px) {
        grid-gap: 40px;
    }
}

img {
    max-width: 100%;
    height: auto;
    display: block;
}

.visually-hidden {
    position: absolute;
    clip: rect(0 0 0 0);
    width: 1px;
    height: 1px;
    margin: -1px;
}

.map-site {
    display: flex;
    flex-direction: column;
    color: #DB1F26;
    gap: 10px;
    padding: 50px 0;
}
